import React from "react";
import {
  // Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@material-ui/core";

function Messages() {
  return (
    <div style={{ margin: 20 }}>
      <TableContainer>
        <Table aria-label="orders">
          <TableHead>
            <TableRow>
              <TableCell> </TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {/* {rows.map((row) => ( */}
            <TableRow key="row.name">
              <TableCell></TableCell>
              <TableCell>//</TableCell>
              <TableCell></TableCell>
              <TableCell> </TableCell>
            </TableRow>
            <TableRow key="row.">
              <TableCell></TableCell>
              <TableCell>//</TableCell>
              <TableCell></TableCell>
              <TableCell> </TableCell>
            </TableRow>
            {/* ))} */}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}

export default Messages;
